.checkout-container .checkout-detail-container .detail-column .detail-group .detail-value {
  font-weight: bold;
  font-size: 12px;
  color: #2C3652; }

.checkout-container .checkout-detail-container .detail-column {
  padding: 20px 0; }
  @media screen and (max-width: 690px) {
    .checkout-container .checkout-detail-container .detail-column {
      padding: 10px 0; } }
  .checkout-container .checkout-detail-container .detail-column .detail-padding-right {
    margin-right: 40px; }
  .checkout-container .checkout-detail-container .detail-column .detail-padding-left {
    margin-left: 40px; }
  @media screen and (max-width: 690px) {
    .checkout-container .checkout-detail-container .detail-column .detail-padding-right {
      margin-right: 0; }
    .checkout-container .checkout-detail-container .detail-column .detail-padding-left {
      margin-left: 0; } }
  .checkout-container .checkout-detail-container .detail-column .detail-header {
    font-weight: bold;
    font-size: 12px;
    color: #2C3652; }
  .checkout-container .checkout-detail-container .detail-column .detail-group {
    margin: 12px 0; }
    @media screen and (max-width: 690px) {
      .checkout-container .checkout-detail-container .detail-column .detail-group {
        margin: 6px 0; } }
    .checkout-container .checkout-detail-container .detail-column .detail-group .detail-desc {
      font-weight: normal;
      font-size: 10px;
      color: #B6BDCB; }
    .checkout-container .checkout-detail-container .detail-column .detail-group .detail-value {
      padding-left: 10px; }

.checkout-container .main-details {
  border-bottom: solid 1px #C8E0E4; }
