.checkout-container .detail-left-container {
  margin-bottom: 20px;
  margin-right: 0; }
  @media screen and (max-width: 991px) {
    .checkout-container .detail-left-container {
      padding: 0 15px; } }

.checkout-container .detail-right-container {
  margin-left: 40px;
  background: white;
  /* Mild shadow */
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05); }
  @media screen and (max-width: 991px) {
    .checkout-container .detail-right-container {
      margin-left: 0; } }

.checkout-container .summary-seperator {
  border-top: 1px solid #CADBEA;
  margin: 20px 0; }
  .checkout-container .summary-seperator:nth-of-type(1) {
    margin-top: 0; }

.checkout-container .card-body {
  padding: 0 !important; }

.checkout-container .company-product-container {
  margin-bottom: -1px;
  border: none;
  border-bottom: solid 1px #C8E0E4; }

.checkout-container .form-header {
  text-align: center;
  font-size: 24px;
  font-weight: 800;
  color: #354F53; }
  @media screen and (max-width: 690px) {
    .checkout-container .form-header {
      font-size: 18px; } }

.checkout-container .summary-container {
  padding: 20px;
  padding-bottom: 50px; }

.checkout-container .charged-header {
  text-align: center;
  font-size: 18px;
  font-weight: 800;
  color: #354F53; }
  @media screen and (max-width: 690px) {
    .checkout-container .charged-header {
      font-size: 16px; } }

.checkout-container .piggy {
  margin-left: 40px; }
  @media screen and (max-width: 991px) {
    .checkout-container .piggy {
      margin-left: 0; } }
