.check-out-popup {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%; }
  .check-out-popup p {
    padding: 0 10px; }
  .check-out-popup img {
    max-width: 90%;
    z-index: -1; }
    @media screen and (max-width: 576px) {
      .check-out-popup img {
        max-width: 60%; } }
  .check-out-popup .bounce-in {
    animation-delay: 3s;
    animation: bounceIn 1s; }
  .check-out-popup .upper-container p,
  .check-out-popup .upper-container span {
    text-align: center;
    font-size: 24px;
    font-weight: 600;
    color: #354F53; }
    @media screen and (max-width: 576px) {
      .check-out-popup .upper-container p,
      .check-out-popup .upper-container span {
        font-size: 18px; } }
  .check-out-popup .upper-container .focus-text {
    color: #00D7AF; }
  .check-out-popup .upper-margin-up {
    margin-top: -50px; }
  .check-out-popup .upper-margin-down {
    margin-top: 30px; }
  .check-out-popup .extra-text p,
  .check-out-popup .extra-text span,
  .check-out-popup .mid-container p,
  .check-out-popup .mid-container span {
    text-align: center;
    font-size: 16px;
    font-weight: 200;
    color: #99BBC1;
    max-width: 350px; }
    @media screen and (max-width: 576px) {
      .check-out-popup .extra-text p,
      .check-out-popup .extra-text span,
      .check-out-popup .mid-container p,
      .check-out-popup .mid-container span {
        font-size: 14px; } }
  .check-out-popup .button-container {
    display: flex;
    margin-top: 27px; }
    .check-out-popup .button-container button {
      margin: 15px; }
    @media screen and (max-width: 576px) {
      .check-out-popup .button-container {
        margin-top: 10px; }
        .check-out-popup .button-container button {
          min-width: 100px !important;
          min-height: 46px !important; }
          .check-out-popup .button-container button p {
            font-size: 16px !important; } }
