.checkout-container .cashback .cashback-text {
  font-weight: bold;
  font-size: 12px;
  color: #2C3652; }

.checkout-container .cashback .cashback-inner {
  display: flex; }
  @media screen and (min-width: 576px) {
    .checkout-container .cashback .cashback-inner {
      transform: translateY(-6px); } }
  .checkout-container .cashback .cashback-inner .checkbox-container-v3 {
    margin-bottom: 0; }

.checkout-container .cashback .cashback-checkbox .input-wrapper-main-new {
  min-height: 0;
  margin-top: 0; }

.checkout-container .cashback .cashback-balance {
  max-width: 112px;
  transform: translateX(20px); }
  @media screen and (max-width: 575px) {
    .checkout-container .cashback .cashback-balance {
      margin-bottom: 10px; } }

.checkout-container .cashback .cashback-balance-charged {
  transform: none; }
