.checkout-container .summary-item-container .summary-item-desc, .checkout-container .summary-item-container .summary-item-value {
  font-weight: bold;
  font-size: 12px;
  color: #2C3652; }

.checkout-container .summary-item-container {
  display: flex;
  justify-content: center;
  align-items: baseline; }
  .checkout-container .summary-item-container .summary-left {
    flex: 1;
    display: flex;
    flex-direction: column; }
  .checkout-container .summary-item-container .summary-item-desc {
    flex: 1; }
  .checkout-container .summary-item-container .summary-item-sub-desc {
    font-weight: normal;
    font-size: 12px;
    color: #B6BDCB; }
  .checkout-container .summary-item-container .final-amount {
    font-size: 24px;
    font-weight: 800; }

.checkout-container .promo input {
  max-width: 80px; }

@media screen and (max-width: 991px) {
  .checkout-container .promo input {
    max-width: none; } }

@media screen and (max-width: 575px) {
  .checkout-container .promo {
    margin-bottom: 20px; } }

.checkout-container .promo .input-wrapper-main-new {
  margin: 0; }

.checkout-container .promo-item {
  align-items: center; }
